  font-weight: 400;
  font-style: italic;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  margin: 0 auto;
  padding: 0;
  min-height: 100%;
  min-width: 320px;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #ffffff;
  color: #000000; }

img {
  max-width: 100%;
  object-fit: cover;
  display: block; }

input,
textarea,
select,
button {
  font: inherit;
  line-height: inherit; }

a {
  color: inherit;
  cursor: pointer; }

a:not([href]) {
  cursor: auto; }

button {
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1366px;
  min-width: 320px;
  padding: 0 7.17%;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      width: 100%;
      padding: 0 3.24%; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 4.68%; } }

.header {
  padding-top: 30px;
  padding-bottom: 18px;
  position: relative; }

.header__top {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-bottom: 15px;
  overflow-y: hidden;
  max-height: 78px;
  min-height: 78px;
  margin-bottom: 26px; }
  .header__top::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #F6F5F4;
    top: 114px;
    left: 0; }

.header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%); }

.search-form {
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9B9997;
  position: relative;
  margin-top: 7px; }
  .search-form input {
    color: #9B9997;
    padding: 5px 6px 5px 30px;
    border: none;
    /*    &::focus{
          outline: none;
        }*/ }
    .search-form input::placeholder {
      font-style: inherit;
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: #9B9997; }
    .search-form input::-ms-clear {
      display: none; }

.search-form__btn {
  position: absolute;
  left: -5px;
  top: 3px;
  background-color: transparent;
  border: none;
  padding: 5px; }

.user-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #1d1613;
  margin-top: 7px;
  max-width: 452px;
  margin-right: -29px; }

.user-nav__login {
  text-decoration: none;
  padding: 5px;
  margin-right: 29px; }

.user-nav__bucket {
  margin: 0;
  padding: 5px 0 5px 5px;
  margin-right: 29px; }
  .user-nav__bucket svg {
    display: none; }

.nav__main-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0 auto; }

.nav__main-item {
  margin-right: 32px; }
  .nav__main-item:last-child {
    margin-right: 0; }

.nav__main-link {
  text-decoration: none;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1d1613; }

.nav__small-list {
  display: none; }

.nav__login {
  display: none; }
